
#sign {
  width: 100%;
  height: 100%;
  text-align: center;
}
#sign .main-wrapper .signin-wrapper {
  padding-top: 15%;
}
#sign .main-wrapper .signin-wrapper .title {
  color: #fff;
  padding-bottom: 48px;
  text-align: center;
  font-size: 18px;
  font-weight: normal;
}
#sign .main-wrapper .signin-wrapper form .form-control {
  display: table;
  width: 320px;
  margin: 0 auto;
  background-color: #fff;
  text-align: left;
  padding: 6px 0;
  box-sizing: border-box;
}
#sign .main-wrapper .signin-wrapper form .form-control.yellow {
  background-color: #faffbd;
}
#sign .main-wrapper .signin-wrapper form .form-control:first-of-type {
  border-top-left-radius: 6px;
  border-top-right-radius: 6px;
  border-bottom: 1px solid #ddd;
}
#sign .main-wrapper .signin-wrapper form .form-control:nth-of-type(2) {
  border-bottom-left-radius: 6px;
  border-bottom-right-radius: 6px;
}
#sign .main-wrapper .signin-wrapper form .form-control .username {
  width: 320px;
  border: 0;
  padding: 0 6px;
  box-sizing: border-box;
}
#sign .main-wrapper .signin-wrapper form .form-control .username.yellow {
  background-color: #faffbd;
}
#sign .main-wrapper .signin-wrapper form .form-control .password {
  width: 290px;
  border: 0;
  padding: 0 6px;
  box-sizing: border-box;
}
#sign .main-wrapper .signin-wrapper form .form-control .password.yellow {
  background-color: #faffbd;
}
#sign .main-wrapper .signin-wrapper form .form-control .enter {
  display: inline-block;
  position: relative;
  width: 30px;
  box-sizing: border-box;
  text-align: center;
  cursor: pointer;
}
#sign .main-wrapper .signin-wrapper form .form-control .enter .iconfont {
  line-height: 24px;
  font-size: 24px;
}
#sign .main-wrapper .signin-wrapper form .form-control .enter .loading-wrapper {
  position: absolute;
  bottom: -24px;
  right: 0;
  width: 38px;
  height: 38px;
  z-index: 999;
}
#sign .main-wrapper .signin-wrapper .signup {
  margin-top: 24px;
  display: inline-block;
  color: #fff;
  padding: 12px 24px;
  cursor: pointer;
}
#sign .main-wrapper .signin-wrapper .signup:hover {
  color: #fa3140;
}


