
#loading {
  position: relative;
  width: 100%;
  height: 100%;
}
#loading .loading-wrapper {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 60px;
  height: 60px;
  text-align: center;
  transform: translate3d(-50%, -50%, 0);
}
#loading .loading-wrapper .point {
  position: absolute;
  width: 1px;
  height: 6px;
  transform-origin: 50% 150%;
  background-color: #999;
}
#loading .loading-wrapper .point:nth-of-type(1) {
  transform: rotateZ(30deg);
  animation: loading 0.6s 0.05s infinite;
}
#loading .loading-wrapper .point:nth-of-type(2) {
  transform: rotateZ(60deg);
  animation: loading 0.6s 0.1s infinite;
}
#loading .loading-wrapper .point:nth-of-type(3) {
  transform: rotateZ(90deg);
  animation: loading 0.6s 0.15s infinite;
}
#loading .loading-wrapper .point:nth-of-type(4) {
  transform: rotateZ(120deg);
  animation: loading 0.6s 0.2s infinite;
}
#loading .loading-wrapper .point:nth-of-type(5) {
  transform: rotateZ(150deg);
  animation: loading 0.6s 0.25s infinite;
}
#loading .loading-wrapper .point:nth-of-type(6) {
  transform: rotateZ(180deg);
  animation: loading 0.6s 0.3s infinite;
}
#loading .loading-wrapper .point:nth-of-type(7) {
  transform: rotateZ(210deg);
  animation: loading 0.6s 0.35s infinite;
}
#loading .loading-wrapper .point:nth-of-type(8) {
  transform: rotateZ(240deg);
  animation: loading 0.6s 0.4s infinite;
}
#loading .loading-wrapper .point:nth-of-type(9) {
  transform: rotateZ(270deg);
  animation: loading 0.6s 0.45s infinite;
}
#loading .loading-wrapper .point:nth-of-type(10) {
  transform: rotateZ(300deg);
  animation: loading 0.6s 0.5s infinite;
}
#loading .loading-wrapper .point:nth-of-type(11) {
  transform: rotateZ(330deg);
  animation: loading 0.6s 0.55s infinite;
}
#loading .loading-wrapper .point:nth-of-type(12) {
  transform: rotateZ(360deg);
  animation: loading 0.6s 0.6s infinite;
}
@-moz-keyframes loading {
from {
    opacity: 1;
}
50% {
    opacity: 0.5;
}
to {
    opacity: 1;
}
}
@-webkit-keyframes loading {
from {
    opacity: 1;
}
50% {
    opacity: 0.5;
}
to {
    opacity: 1;
}
}
@-o-keyframes loading {
from {
    opacity: 1;
}
50% {
    opacity: 0.5;
}
to {
    opacity: 1;
}
}
@keyframes loading {
from {
    opacity: 1;
}
50% {
    opacity: 0.5;
}
to {
    opacity: 1;
}
}


